import React, { PureComponent } from 'react'
import { message, Modal, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { API_CREATEPRODUCTIONORDERS_IMPORT_BATCH } from 'api'
import { handleDownLoadTemplate } from 'util'
import { Import } from 'HOC'
import { array } from 'prop-types'
// import { Import } from '@csx/component'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.productOrder }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 新增-手动创建-导入
  handleAdd = () => {
    const { history } = this.props
    history.push('/mms/productCenter/productOrder/create')
  }
  // 新增-自动创建-查询
  handleAddAuto = () => {
    const { history } = this.props
    history.push('/mms/productCenter/productOrder/search')
  }

  // 导出
  handleExport = () => {
    const { handleExport, searchParams } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }

    const { initDate = '', produceTimeStartDate, produceTimeEndDate } = reqObj
    let flag =
      initDate && Array.isArray(initDate) && initDate.length > 0 ? true : false
    delete reqObj.initTime
    delete reqObj.initDate
    if (flag) {
      handleExport(
        { searchParams: { produceTimeStartDate, produceTimeEndDate } },
        () => {
          message.success('导出成功')
        }
      )
    } else {
      message.error('请选择生产日期后再执行导出操作 ')
    }
  }
  handleOperator = () => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleCloseOrder,
      selectedRowKeys,
      selectedRows,
      update
    } = this.props
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    // 关单操作中 选项中必须全部为未生产状态
    let flag = true
    selectedRows &&
      selectedRows.length > 0 &&
      selectedRows.map((item, index, arr) => {
        const { orderStatus = '' } = item
        if (orderStatus != 0) {
          flag = false
        }
      })
    if (!flag) {
      return message.error('选项中必须全部为[未生产]状态,请核实！')
    }
    confirm({
      title: '温馨提示',
      content: '确定关闭工单？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleCloseOrder(
          {
            data: selectedRowKeys
          },
          () => {
            update({ selectedRowKeys: [] })
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
    /* 导入成功 */
  handleImportSuccess = res => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      update
    } = this.props
    update({
      tableLoading: false,
      modalTuttonLoading_import: false
    })
    handleGetList({ searchParams: { page, size, ...searchParams } })
    res && res.message === 'ok'
        ? message.success('导入成功')
        : res.message
        ? message.success(res.message)
        : ''
  }
    /* 报错导出 */
  handleExprotError = () => {
    const { update } = this.props
    update({
      tableLoading: false,
      modalTuttonLoading_import: false
    })
  }
  render() {
    const {
      menuPermissionIds,
      modalTuttonLoading,
      modalTuttonLoading_import
    } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds && menuPermissionIds.includes('productOrder_closeOrder') &&
          <Button
            type='primary'
            className={style.btn}
            onClick={this.handleOperator}
          >
            关闭工单
          </Button>
        }
        {menuPermissionIds && menuPermissionIds.includes('productOrder_exportActualOutput') &&
          <Button
            type='primary'
            className={style.btn}
            onClick={this.handleExport}
            loading = {modalTuttonLoading}
          >
            导出实际产量
          </Button>
        }
        {menuPermissionIds && menuPermissionIds.includes('productOrder_createOrderManual') &&
          <Button
            type='primary'
            className={style.btn_right}
            onClick={this.handleAdd}
          >
            手动创建订单
          </Button>
        }

        {/* <Button type="primary" className={style.btn}  onClick={this.handleAddAuto}>自动创建订单</Button> */}
      </div>
    )
  }
}



