import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import OperationModal from './components/OperationModal'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
import { formatterPathAndId } from '../global/components/menuData'
import { getPermissions } from 'util'
@connect(
  state => ({
    ...state.productOrder,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)

export default class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillMount() {
    const { location = {} } = this.props
    const { pathname } = location
    const newKey = pathname.replace(/^\//, '') // 去除开头的斜线('/')
    const b2bUserMenu = JSON.parse(localStorage.getItem('b2bUserMenu') || '[]')
    let id = formatterPathAndId(b2bUserMenu, newKey)
    console.log(2222)
    // 由于该页面中，可以通过页面内的点击按钮跳转到别的页面 ，因此也就绕过了
    if (id) {
      getPermissions(id).then(res => {
        this.props.updateUser({ menuPermissionIds: res || '' })
      })
    }
  }
  componentWillUnmount() {
    this.props.update({
      searchResult: [],
      searchParams: {},
      productCodeOptions: {}
    })
  }

  render() {
    const { history, menuPermissionIds = [] } = this.props
    return (
      <section >
        <Bread route={['生产中心', '生产订单']} />
        <Form menuPermissionIds ={menuPermissionIds}/>
        <Tool history={history} menuPermissionIds ={menuPermissionIds}/>
        <Table menuPermissionIds ={menuPermissionIds}/>
        {this.props.isShowModal ? <OperationModal /> : null}
      </section>
    )
  }
}



