import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_PRODUCTORDERS_PAGE,
  API_PRODUCTORDERSS_RELATEDATA,
  API_PRODUCTORDERS_CREATE,
  API_PRODUCTORDERS_CLOSE,
  API_PRODUCTORDERS_UPDATE,
  API_PRODUCTORDERSS_EXPORT,
  API_PRODUCTORDERS_DET_MATERIAL_RESEND,
  API_PRODUCTORDERS_DET_BATCH_REQUIREMENT,
  API_PRODUCTORDERS_DET_PRODUCT_NUMBER,
  API_PRODUCTORDERS_DET_MATERIAL_REQUIREMENT
} from 'api'
//同步
export const update = object => {
  console.log('object is', object)
  return {
    type: actionType.PRODUCTORDERS_UPDATE,
    data: object
  }
}
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.PRODUCTORDERS_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_PRODUCTORDERS_PAGE, ...reqData })
    .then(data => {
      console.log('data result is', data)
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '', list = '' } = result
      dispatch({
        type: actionType.PRODUCTORDERS_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total,
          searchResult: list
        }
      })
    })
    .catch(err => {
      console.error(err)
      dispatch({
        type: actionType.PRODUCTORDERS_FAIL
      })
    })
}

// 创建/编辑
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.PRODUCTORDERS_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_PRODUCTORDERS_UPDATE
      : API_PRODUCTORDERS_CREATE
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.PRODUCTORDERS_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTORDERS_FAIL
      })
    })
}

