// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.PRODUCTORDERS_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_PRODUCTORDERSS_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.PRODUCTORDERS_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTORDERS_FAIL
      })
    })
}
// 获取 - 引用数据
export const getRelateData = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({ api: API_PRODUCTORDERSS_RELATEDATA, ...reqData })
    .then(data => {
      callBack && callBack(data)
      console.log(' data is 1111', data)
      const { result } = data
      dispatch({
        type: actionType.PRODUCTORDERS_UPDATE,
        data: {
          relatedDataObj: result
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTORDERS_FAIL
      })
    })
}



