import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, DatePicker, Input } from 'antd'
import style from './style/Form.scss'
import { CsxSelect, CsxSearch } from 'Form'
import { connect } from 'react-redux'
import {
  API_GET_LOCATION_CODEANDNAME,
  API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST,
  API_GET_PRODUCTION_CODEANDNAME,
  API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST
} from 'api'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const { RangePicker } = DatePicker
import moment from 'moment'
@connect(
  state => ({ ...state.productOrder }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {}
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams = {}, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams = {}, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    delete reqObj.initTime
    delete reqObj.initDate
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }

  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {}, productCodeOptions: {} })
  }
  render() {
    const { searchParams = {}, productCodeOptions = {} } = this.props
    const {
      taskOrderCode = '',
      locationCode = '',
      produceLineCode = '',
      status = '',
      initTime = '',
      initDate = '',
      batchNumber = '',
      remark = ''
    } = searchParams
    const statusOptions = [
      { key: '0', value: '未生产' },
      { key: '1', value: '已生产' },
      { key: '2', value: '已调拨' },
      { key: '3', value: '已关闭' },
      { key: '4', value: '调拨完成' }
    ]
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>生产工单号：</label>
              <div className='box '>
                <Input
                  value={taskOrderCode}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ taskOrderCode: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_GET_LOCATION_CODEANDNAME}
              label='地点编码/名称'
              allowClear={true}
              selectItem={{
                data: locationCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                console.log('value is', value)
                this.onInputChange({ locationCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_GET_PRODUCTION_CODEANDNAME}
              label='商品名称/编码'
              searchParamsKey='product'
              allowClear={true}
              selectItem={{
                data: productCodeOptions,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={data => {
                const { value = '' } = data
                const { update } = this.props
                update({
                  searchParams: { ...searchParams, productSearchKey: value },
                  productCodeOptions: data
                })
              }}
              onSelect={data => {
                const { update } = this.props
                const { value = '' } = data
                update({
                  searchParams: { ...searchParams, productSearchKey: value },
                  productCodeOptions: data
                })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST}
              label='产线编码'
              allowClear={true}
              selectItem={{
                data: produceLineCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ produceLineCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              data={statusOptions}
              allowClear={true}
              label='订单状态'
              selectItem={{ data: status, key: 'key', valueKey: ['value'] }}
              onChangeSelect={value => {
                this.onInputChange({ status: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST}
              label='生产波次'
              allowClear={true}
              selectItem={{
                data: batchNumber,
                key: 'code',
                valueKey: ['name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ batchNumber: value })
              }}
            />
          </Col>
          <Col span={8}>
            <div className={'commonInput isTimePicker'}>
              <label>生产日期：</label>
              <div className='box' style={{ marginRight: 0 }}>
                <RangePicker
                  allowClear={true}
                  // showTime
                  value={initDate}
                  onChange={(time, string) => {
                    let produceTimeStartDate =
                      time &&
                      time[0] &&
                      `${moment(time[0]).format('YYYY-MM-DD')} 00:00:00`
                    let produceTimeEndDate =
                      time &&
                      time[1] &&
                      `${moment(time[1]).format('YYYY-MM-DD')} 23:59:59`
                    this.onInputChange({
                      produceTimeStartDate,
                      produceTimeEndDate,
                      initDate: time
                    })
                  }}
                  format='YYYY-MM-DD'
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className={'commonInput isTimePicker showTimePicker width_320'}>
              <label>创建日期：</label>
              <div className='box' style={{ marginRight: 0 }}>
                <RangePicker
                  allowClear={true}
                  showTime
                  mode={['date', 'date']}
                  value={initTime}
                  onChange={(time, string) => {
                    let startDate =
                      time &&
                      time[0] &&
                      `${moment(time[0]).format('YYYY-MM-DD HH:mm:ss')}`
                    let endDate =
                      time &&
                      time[1] &&
                      // `${moment(time[1]).format('YYYY-MM-DD HH:mm:ss')} 23:59:59`
                      `${moment(time[1]).format('YYYY-MM-DD HH:mm:ss')}`
                    this.onInputChange({
                      startDate,
                      endDate,
                      initTime: time
                    })
                  }}
                  format='YYYY-MM-DD HH:mm:ss'
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>备注：</label>
              <div className='box '>
                <Input
                  value={remark}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ remark: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8} push={16}>
            <Row type='flex' justify='end'>
               <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
                >
                  查询
                </Button>
                <Button type='primary' onClick={this.reset}>
                重置
                </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



