import React, { PureComponent } from 'react'
import { Modal, Row, Col, Form, message, Table } from 'antd'
import {
  API_SYSPROLINECONFIGS_LIST_NO_ASSOCIATION,
  API_SYSPROLINECONFIGS_TERMINAL_LOCATION_LIST
} from 'api'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import style from './style/Tool.scss'
import { countTableScroll } from 'util'
const bomTypeList = {
  1: '组合型',
  2: '分解型',
  3: '粗加工'
}
const feeTypeList = {
  1: '小时',
  2: '元'
}
@connect(
  state => ({ ...state.productOrder, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    // 数据初始化
    const { modalData, getRelateData } = this.props
    if (modalData) {
      //编辑
      const { taskOrderCode = '' } = modalData
      const params = { searchParams: { orderCode: taskOrderCode } }
      getRelateData(params, data => {})
    }
  }
  handleSubmit = e => {
    const { form, handleAddOrEdit, dataLocationList, workType } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      console.log('form values is', values)
      let reqObj = { ...values }
      if (!err) {
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'location') {
              let data = reqObj[i].children
              // reqObj.location = reqObj[i].children
              reqObj.locationCode = data && data.split(' ')[0]
              reqObj.locationName = data && data.split(' ')[1]
            }
          }
        }
        let result = {
            data: {
              ...reqObj,
              itemsList: dataLocationList || []
            }

          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (workType && workType === 'edit') {
          result.data.workType = 'edit'
          msg = '编辑成功'
        }
        delete result.location
        delete result.locationList
        handleAddOrEdit({ ...result }, data => {
          message.success(msg)
          const { handleGetList, searchParams, page, size } = this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  renderColumns = () => {
    const { modalData = {} } = this.props
    const { bomType = '' } = modalData
    let nameOfCode = '原料编码',
      nameOfPro = '原料名称'
    if (bomType == '2') { // 分解型
      nameOfCode = '成品编码'
      nameOfPro = '成品名称'
    }
    const bom_columns = [
      {
        title: nameOfCode,
        width: 100,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: nameOfPro,
        width: 150,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '评估类',
        width: 80,
        dataIndex: 'mrpProp',
        key: 'mrpProp'
      },
      {
        title: '单位',
        width: 80,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '规格',
        width: 80,
        dataIndex: 'spec',
        key: 'spec'
      },
      {
        title: '数量',
        width: 80,
        dataIndex: 'number',
        key: 'number'
      },
      {
        title: '报废率（%）',
        width: 80,
        dataIndex: 'scrapRate',
        key: 'scrapRate'
      }

    ]
    if (bomType == '2') { // 分解型
      bom_columns.splice(
        -1, 1,
        {
          title: '价值占比（%）',
          width: 80,
          dataIndex: 'valueShare',
          key: 'valueShare'
        }
      )
    }

    const general_bom_columns = [
      {
        title: '产线编码',
        width: 100,
        dataIndex: 'lineCode',
        key: 'lineCode'
      },
      {
        title: '产线名称',
        width: 100,
        dataIndex: 'lineName',
        key: 'lineName'
      },
      {
        title: '数量因子',
        width: 80,
        dataIndex: 'reckonFactor',
        key: 'reckonFactor'
      },
      {
        title: '机时',
        width: 80,
        dataIndex: 'machineFee',
        key: 'machineFee',
        render: (text, record, index) => `${text} ${record && feeTypeList[record.machineFeeUnit]}`
      },
      {
        title: '工时',
        width: 80,
        dataIndex: 'workerFee',
        key: 'workerFee',
        render: (text, record, index) => `${text} ${record && feeTypeList[record.supportMaterialFeeUnit]}`
      },
      {
        title: '辅料',
        width: 80,
        dataIndex: 'supportMaterialFee',
        key: 'supportMaterialFee',
        render: (text, record, index) => `${text} ${record && feeTypeList[record.workerFeeUnit]}`
      }
    ]

    const industry_columns = [
      {
        title: '商品编码',
        width: 100,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '商品名称',
        width: 150,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: 'BOM类型',
        width: 80,
        dataIndex: 'bomType',
        key: 'bomType'
      },
      {
        title: '产线编码',
        width: 100,
        dataIndex: 'lineCode',
        key: 'lineCode'
      },
      {
        title: '产线名称',
        width: 150,
        dataIndex: 'lineName',
        key: 'lineName'
      },
      {
        title: '数量因子',
        width: 80,
        dataIndex: 'reckonFactor',
        key: 'reckonFactor'
      },
      {
        title: '机时',
        width: 80,
        dataIndex: 'machine',
        key: 'machine',
        render: (text, record, index) => text
      },
      {
        title: '工时',
        width: 80,
        dataIndex: 'manually',
        key: 'manually'
      },
      {
        title: '辅料',
        width: 80,
        dataIndex: 'accessories',
        key: 'accessories',
        render: (text, record, index) => text
      }
    ]
    const workType_columns = [
      {
        title: '产线',
        width: 100,
        dataIndex: 'lineName',
        key: 'lineName'
      },
      {
        title: '类型',
        width: 60,
        dataIndex: 'feeTypeName',
        key: 'feeTypeName'
      },
      {
        title: '单位',
        width: 80,
        dataIndex: 'unit',
        key: 'unit',
        render: (text) => feeTypeList[text]
      },
      {
        title: '固定价格（元）',
        width: 100,
        dataIndex: 'price',
        key: 'price'
      },
      {
        title: '成本要素',
        width: 100,
        dataIndex: 'cost',
        key: 'cost'
      },
      {
        title: '更新人',
        width: 80,
        dataIndex: 'updateBy',
        key: 'updateBy'
      },
      {
        title: '更新时间',
        width: 120,
        dataIndex: 'updateTime',
        key: 'updateTime',
        render: (text, record, index) => text
      }
    ]

    return {
      bom_columns,
      general_bom_columns,
      industry_columns,
      workType_columns
    }
  }
  renderBomData() {
    const {
      relatedDataObj = {},
      tableLoading
    } = this.props
    const {
      bomOneResponse = {},
      orderCode = '',
      locationCode = ''
    } = relatedDataObj
    const {
      productCode = '',
      productName = '',
      unit = '',
      spec = '',
      bomType = '',
      calcFactor = ''
    } = bomOneResponse
    const {
      bom_columns = []
    } = this.renderColumns()
    let nameOfCode = '成品编码',
      nameOfPro = '成品名称'
    if (bomType == '2') {
      nameOfCode = '原料编码'
      nameOfPro = '原料名称'
    }

    return (
      <div className={style.modalData}>
      <Row className={style.headerLine}>
        <Col span={12}>
          <div>
            <span>生产工单号：</span>
            <span>{orderCode}</span>
          </div>
        </Col>
        <Col span={12}>
          <div>
            <span>地点编码：</span>
            <span>{locationCode}</span>
          </div>
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <div className={style.header_item}>
            <span>BOM设置</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>BOM类型：</span>
            <span>{bomTypeList[bomType]}</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>计算因子：</span>
            <span>{calcFactor}</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>{nameOfCode}：</span>
            <span>{productCode}</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>{nameOfPro}：</span>
            <span>{productName}</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>单位：</span>
            <span>{unit}</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>规格：</span>
            <span>{spec}</span>
          </div>
        </Col>
      </Row>
      <Row>
      <div className={`table-group ${style.tableModal}`}>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={ bom_columns }
          dataSource={bomOneResponse && bomOneResponse.bomAbilityOneList}
          scroll={{ x: countTableScroll(bom_columns) }}
          pagination={false}
        />
      </div>
      <Col span={6} offset={12}>
        <div>
          <span>更新人：</span>
          <span>{bomOneResponse && bomOneResponse.updateBy}</span>
        </div>
      </Col>
      <Col span={6}>
        <div>
          <span>更新时间：</span>
          <span>{ bomOneResponse && bomOneResponse.updateTime}</span>
        </div>
      </Col>
      </Row>

    </div>

    )
  }
  renderIndustryData() {
    const {
      relatedDataObj = {},
      tableLoading
    } = this.props
    const {
      craftQueryResponse = {},
      bomOneResponse = {}
    } = relatedDataObj
    const {
      bomType = ''
    } = bomOneResponse

    const {
      industry_columns = []
    } = this.renderColumns()
    return (
      <div className={style.modalData}>
      <Row>
      <Col span={24}>
        <div className={style.header_item}>
        <span>工艺路线设置</span>
        </div>
      </Col>
      <div className={`table-group ${style.tableModal}`}>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={ industry_columns }
          dataSource={craftQueryResponse && [{ ...craftQueryResponse, bomType: bomTypeList[bomType] }]}
          scroll={{ x: countTableScroll(industry_columns) }}
          pagination={false}
        />
      </div>
      <Col span={6} offset={12}>
        <div>
          <span>更新人：</span>
          <span>{craftQueryResponse && craftQueryResponse.updateBy}</span>
        </div>
      </Col>
      <Col span={6}>
        <div>
          <span>更新时间：</span>
          <span>{craftQueryResponse && craftQueryResponse.updateTime}</span>
        </div>
      </Col>
      </Row>

    </div>

    )
  }
  renderGeneralBomData() {
    const {
      relatedDataObj = {},
      tableLoading
    } = this.props
    const {
      generalBomResponse = {},
      bomOneResponse = {}
    } = relatedDataObj
    const {
      calcFactor = '',
      largeCategoryName = '',
      middleCategoryName = '',
      smallCategoryName = '',
      scrapRate = ''
    } = generalBomResponse
    const {
      general_bom_columns = []
    } = this.renderColumns()
    return (
      <div className={style.modalData}>
      <Row>
      <Col span={24}>
        <div className={style.header_item}>
        <span>通用BOM设置</span>
        </div>
      </Col>
      <Col span={6}>
          <div>
            <span>BOM类型：</span>
            <span>{'通用BOM'}</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>计算因子：</span>
            <span>{calcFactor}</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>商品分类：</span>
            <span>{`${largeCategoryName} - ${middleCategoryName} - ${smallCategoryName}`}</span>
          </div>
        </Col>
        <Col span={6}>
          <div>
            <span>报废率（%）：</span>
            <span>{scrapRate}</span>
          </div>
        </Col>
      <div className={`table-group ${style.tableModal}`}>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={ general_bom_columns }
          dataSource={generalBomResponse && [{ ...generalBomResponse }]}
          scroll={{ x: countTableScroll(general_bom_columns) }}
          pagination={false}
        />
      </div>
      <Col span={6} offset={12}>
        <div>
          <span>更新人：</span>
          <span>{generalBomResponse && generalBomResponse.updateBy}</span>
        </div>
      </Col>
      <Col span={6}>
        <div>
          <span>更新时间：</span>
          <span>{generalBomResponse && generalBomResponse.updateTime}</span>
        </div>
      </Col>
      </Row>

    </div>

    )
  }
  renderWorkTypeData() {
    const {
      relatedDataObj = {},
      tableLoading
    } = this.props
    const {
      taskFeeQueryResponseList = []
    } = relatedDataObj
    const {
      workType_columns = []
    } = this.renderColumns()
    return (
      <div className={style.modalData}>
      <Row>
        <Col span={24}>
          <div className={style.header_item}>
          <span>作业类型费用设置</span>
          </div>
        </Col>
        <div className={`table-group ${style.tableModal}`}>
          <Table
            rowKey={record => record.id}
            loading={tableLoading}
            columns={ workType_columns }
            dataSource={taskFeeQueryResponseList}
            // scroll={{ x: countTableScroll(workType_columns) }}
            pagination={false}
          />
        </div>
      </Row>

    </div>

    )
  }
  render() {
    const {
      modalTitle,
      relatedDataObj = ''
    } = this.props
    const {
      bomOneResponse = '',
      generalBomResponse = '',
      taskFeeQueryResponseList = '',
      craftQueryResponse = ''
    } = relatedDataObj
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        maskClosable={true}
        className='operationModal_relateData'
        footer={null}
        width='1000px'
      >
      {bomOneResponse && this.renderBomData()}
      {craftQueryResponse && this.renderIndustryData()}
      {generalBomResponse && this.renderGeneralBomData()}
      {taskFeeQueryResponseList && this.renderWorkTypeData()}
     </Modal>
    )
  }
}
export default Form.create()(App)



